.icoplus {
  background-color: #fff;
  width: 100%;
  max-width: 500px;
  height: 100px;
  border-radius: 10px;
  margin: 0px;
  cursor: pointer;
}

.list_wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 10px 0;
}
.list_wrap .list_area {
  width: 100%;
  margin: 0 10px;
  padding: 15px;
  border: 3px solid #222;
}
.list_wrap .list_area .title {
  font-size: 22px;
  font-weight: 900;
}
