.input_text {
  box-sizing: border-box;
  border: 2px solid #ccc; /* 테두리 스타일 */
  border-radius: 5px; /* 테두리 둥글기 */
  padding: 10px; /* 내부 여백 */
  margin: 5px 0;
  font-size: 16px; /* 폰트 크기 */
  resize: none; /* 크기 조절 금지 */
  width: 100%; /* 폭 100% */
  max-width: 500px;
  height: 50px;
}
.ico_plus {
  background-color: #222;
  width: 100%;
  max-width: 500px;
  height: 100px;
  border-radius: 10px;
  margin: 0px;
  cursor: pointer;
}
