.item_wrap {
  position: relative;
  width: 33vw;
  margin: 20px 0;
  padding: 10px;
  border: 2px solid #aaa;
  border-radius: 10px;
  max-width: 500px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
  box-sizing: border-box;
}
.item_wrap .item-title {
  font-size: 24px;
  font-weight: 700;
  cursor: pointer;
}
.item_wrap .item-desc {
  font-size: 18px;
  cursor: pointer;
}
